.video{
  position: absolute;
  width: 100%;
  height:100vh;
  left: 50%;
  top: 50%;
  object-fit: cover;
  transform: translate(-50%, -50%);
  z-index: -1;
  filter: brightness(40%);
  clip-path: ellipse(162% 100% at 51.12% 0%);
}
Navbar{
  z-index: 2;
}
Corousel{
  background-color: black;
}

